import React, {Component} from "react";
import stylestwo from "./Footer.module.css";
import {Button, Modal, ModalHeader, ModalBody, Collapse, Form, FormGroup, Input, Label} from 'reactstrap';


class Footer extends Component {
    constructor(props){
        super(props)
        this.state= {
            isModalOpen: false
        };

        this.toggleModal = this.toggleModal.bind(this);

    }

toggleModal(){
    this.setState({
        isModalOpen: !this.state.isModalOpen
    });
}

    render(){
        return(
        <React.Fragment>
        <div className={stylestwo.bottom}>
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-auto text-center">
                <p>Data fetched from https://covidtracking.com/data/api</p>
                </div>
                <div className="row justify-content-center">
                <div className="col-auto text-center">
                <div className={stylestwo.button}>
                    <Button outline 
                    color="secondary"  onClick={this.toggleModal}>
                    <i class="fa fa-sign-in fa-lg" aria-hidden="true"></i>Sign Up
                    </Button>
                <p> Get updates delivered directly to your inbox</p>

                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Sign Up for Daily CA Covid Updates</ModalHeader>
                    <ModalBody>
                    <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="name">Name</Label>
                                <Input type="text" id="name" name="name"
                                    innerRef={input => this.name = input} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="email">Email Address</Label>
                                <Input type="email" id="email" name="email"
                                    innerRef={input => this.email = input} />
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Subscribe</Button>
                        </Form>
                    </ModalBody>
                </Modal>

                </div>
                </div>
                </div>
            </div>
            </div>
          
            </div>
        </React.Fragment>
        );
    }

}
export default Footer;