import React, { Component } from "react";
import Axios from "axios";
import styles from "./GetStats.module.css";

class GetStats extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: 0,
      positive: 0,
      hospitalized: 0,
      death: 0,
      deathIncrease: 0,
      hospitalizedIncrease: 0,
    };
  }

  componentDidMount() {
    this.getData();
  }

  async getData() {
    const res = await Axios.get(
      "https://covidtracking.com/api/v1/states/ca/current.json"
    );
    this.setState({
      date: res.data.date,
      positive: res.data.positive,
      hospitalized: res.data.hospitalizedCurrently,
      death: res.data.death,
      deathIncrease: res.data.deathIncrease,
      hospitalizedIncrease: res.data.hospitalizedIncrease,
    });
  }

  render() {
    return (
      <div className="container">
        <div className={styles.header}>
          <div className="row">
         <div className="col">
          <h1 className={styles.headerText}>California Corona Daily Update
            <img className={styles.virus} src={require('./covid19.png')} alt="virus" />
          </h1>
          <h2 className={styles.headerText}>Today's California Covid Stats</h2>
          </div>
        </div>
        </div>
        <div className="container ">
        <div className="row justify-content-center">
          <div className={styles.death}>
          <div className="col">
            <h3>
              Total number of people who have died as a result of COVID-19 so
              far:
            </h3>
          
            <h2 className={styles.stat}>{this.state.death}</h2>
          </div>
          </div>
          <div className= {styles.positive}>
          <div className="col text-center">
            <h3>
              Total # of Californians who have tested positive for COVID-19 so
              far:
            </h3> 
            <h2 className={styles.stat}>{this.state.positive}</h2>
            </div>
          </div>
          <div className={styles.hospitalized}>
          <div className="col text-center ">
            <h3>
              Number of Californians in the hospital for COVID-19 on this day:
            </h3>
            <h2 className={styles.stat}>{this.state.hospitalized}</h2>
          </div>
          </div>
          </div>
        </div>
        </div>
    );
  }
}

export default GetStats;
